@media only screen and (max-width: 600px)
{
	.avatar{
		position: relative;
		width: 80vw;
		height: 80vw;
		justify-content: center;
		overflow: hidden;
		margin: 0 auto;

	}
	.avatar img{
		width: 100%;
		border-radius: 90%;
	}
	.nav::before, .navDark::before{
		width: 80vh;
		height: 80vh;
		background: #eceff1;
		top: -65vh;
		left: -35vh;
	}
	.nav::after, .navDark::after{
		width: 81vh;
		height: 81vh;
		border: 4px dashed #eceff1;
		top: -65vh;
		left: -35vh;
	}
	.l5{
		border: none;
	}
	.l5.push-l7{
		border: none;
	}
	.valign-wrapper{
		display: block!important;
	}
	.expertise-first-left img{
		width: 100%;
	}
	.expertise-first-right{
		margin: 0!important;
	}

	.right-align{
		text-align: left!important;
	}

	
